@font-face {
  font-family: meriwether;
  src: url(assets/fonts/Merriweather-Bold.ttf);
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Poppins",Arial, Helvetica, sans-serif;
  font-weight: bolder;
}

.spech {
 
}
@keyframes writeup {
  from {
    width: 0.2%;
  }

  to {
    width: 100%;
    border-right: none;
  }
}


@keyframes carriet  {
  from, to {border-color: transperent}
  50% {border-color: #8490ff}

}

@keyframes emojiKey {
  from {
    transform: rotate(-2deg);
  }
  to {transform: rotate(2deg);}
}
@keyframes moves{
  from {
    translate: -1000px 0px
  }
  to {
    translate: 0px 0px;
  }
}
@keyframes animate-up {
  from {
    translate: 0px -1000px;
  }
  to {
    translate: 0px 0px;
  }
}

.cert:hover img {
  transform: scale(1.4, 1.4);
}
.skill-me:hover img {
  transform: scale(1.1, 1.1);
}
.animate-up {
  animation:  animate-up 1.5s ease-in
}
.animate-move {
  animation:  moves 1.2s ease-in ;

}
.scale-me:hover {
  transform: scale(1.1, 1.1);
}
.emoji {
  animation:  emojiKey 2s alternate-reverse infinite;
}
.footbar {
  background-color: black;
  color: white !important
}
.name-write {
  max-width: fit-content;
  white-space: nowrap;
  overflow: hidden;
  animation: writeup 3s steps(40, end), carriet 0.75s infinite;
}
.nnav > li > a {
  min-width: 8rem;
  transition:  0.5s;
  text-align: center;
  color : white !important;
}

.nnav > li > a:hover {
  letter-spacing: 1.2px;
  background-color: #ffffff !important;
  color: #33342B !important
}
.abt {
