section > article > section:hover {
  color : #8490ff;
}
.center {
  margin-left: auto;
  margin-right: auto;
  display: block;
  width: 250px;
  
}



.skill-image {
  width: 50px;
  height: 50px;
  object-fit: contain;
 
}
.skillDescription {
  font-weight: bold;
}
.skillE {
  border: 2px solid blueviolet;
  height: 100%;
}
.skill-des {
  position: relative;
  transition: all 0.5s;
  z-index: 5;
  padding: 5px;
  overflow: hidden;
  border-radius: 10px;
  width: 150px;
  color: white !important;

}
.footer {
  color: white;
  min-height: 20vh;
  padding-top: 2vh;
}
.footer a {
 color: white;
}
.projects {
 
}
.cert {
  -webkit-box-shadow: 0px -4px 3px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 0px -4px 3px rgba(50, 50, 50, 0.75);
  box-shadow: 0px -4px 3px rgba(182, 178, 178, 0.75);
  padding: 5px;
  color: white;
  background-color: #099999;
  min-height: 50vh;
}
.brand {
  color: white;
  font-weight: bolder;
}
.cert-description {
  text-align: left;
  min-height: 35vh;
}
.cert:hover {
  color:white;
}

.btn-cert {
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
  transition: all 1s;
  z-index: 1;
  width: 200px;
  overflow: hidden;
}
.btn-cert::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgb(9, 5, 5);
  transition: all 1s;
  transform: translateX(-100%);
  z-index: -1;
}
.btn-cert:hover::before {
 transform: translateX(0);
}
.btn-cert:hover{
  color: white;
  font-weight: bold;
}
.skill-des::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #215b64;;
  transition: all 0.5s;
  transform: translateX(-100%);
  z-index: -1;
}
.skill-des:hover::before {
  transform: translateX(0);
}
.skill-des:hover {
  color: white;
  text-wrap: nowrap;
}
